<template>
  <div class="container mt-3">
    <small class="text-muted">{{ $options.__file }}</small>
    <h1 class="text-primary font-weight-bold">
      <router-link to="/"><img src="~bootstrap-icons/icons/caret-left-fill.svg" alt="caret icon" width="30" height="30"></router-link>
      Vetrina digitale di: {{ store.company_name }}
    </h1>
    <hr/>
    <div class="row alert-danger">TODO</div>
    <div class="row m-3">
      <div class="col">
        <pre>{{ store }}</pre>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      store_id: null,
      store: {}
    }
  },
  methods: {
    async fetchStore (storeId) {
      const response = await fetch('/api/stores/' + storeId, {
        mode: 'no-cors',
        headers: {
          'accept': 'application/json'
        }
      }
      )
      return response.json()
    }
  },
  mounted: function () {
    this.store_id = this.$route.params.id
    this.fetchStore(this.store_id).then(data => { this.store = data })
  }
}
</script>